html {
  overflow-x: hidden;
}

p {
  font-size: 20px;
  font-weight: 400;
}

i {
  border: solid rgb(31, 34, 16);
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

