body {
  overflow-x: hidden;
}

body .base {
  z-index: 90;
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: black; /* for top and bottom menu color*/
  width: 98px;
  height: 98px;
  transition: all 1s cubic-bezier(0.5, -0.75, 0.05, 1);
  border-bottom-right-radius: 100%;
  box-shadow: 2px 2px 10px gold;
}

body .base .menu {
  z-index: 100;
  background-color: rgb(64, 89, 248);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100px;
  height: 100px;
  border-bottom-right-radius: 200px;
  cursor: pointer;
  transition: all 1s cubic-bezier(0.5, -0.75, 0.05, 1), background-color 1s ease;
}

body .base .menu .icon {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-100%, -100%);
}

body .base .menu .icon:before,
body .base .menu .icon:after {
  content: "";
  transform: rotate(0deg);
  transition: top 0.5s ease 0.5s, transform 0.5s ease, background-color 0.75s ease 0.25s;
}

body .base .menu .icon .bar,
body .base .menu .icon:before,
body .base .menu .icon:after {
  position: absolute;
  height: 4px;
  left: -10px;
  right: 5px;
  border-radius: 5px;
  background-color: black;
  transform: rotate(-360deg);
  transition: 1s all;
}

body .base .menu .icon .bar {
  transition: opacity 0s linear 0.5s, background-color 0.75s ease 0.25s;
  opacity: 1;
  top: 10px;
}

body .base .menu .icon:before {
  top: 0px;
}

body .base .menu .icon:after {
  top: initial;
  top: 20px;
}

body .base .page-option {
  z-index: 98;
  position: absolute;
  top: 0px;
  left: 0px;
}

body .base .page-option > * {
  position: absolute;
  font-size: 15px;
  color: black;
  transition: 0.3s cubic-bezier(0.5, -0.25, 0.05, 1);
}

body .base .page-option .about-page {
  top: 35px;
  left: -150px;
  color: #fff;
  transition-delay: 0.2s;
}

body .base .page-option .project-page {
  top: 0px;
  left: -150px;
  color: #fff;
  transition-delay: 0.1s;
}

body .base .page-option .resume-page {
  top: 0px;
  left: -150px;
  color: #fff;
}

body .base .section {
  z-index: 96;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 0px;
  width: 0px;
  transform-origin: 100% 100%;
  transform: rotate(135deg);
}

body .base .section .cover1 {
  transform-origin: 100% 100%;
}

body .base .section .cover1,
body .base .section .cover1 .cover2,
body .base .section .cover1 .cover2 .content {
  position: absolute;
  width: 600px;
  height: 600px;
}

body .base .section .cover1,
body .base .section .cover1 .cover2 {
  top: 50%;
  left: 50%;
  transform: translate(-100%, -100%) rotate(4deg);
  overflow: hidden;
  pointer-events: none;
  transition: transform 0.5s ease-in;
}

body .base .section .cover1 .cover2 {
  transform: translate(50%, -50%) rotate(-8deg);
  transform-origin: 0% 100%;
}

body .base .section .cover1 .cover2 .content {
  width: 150px;
  height: 150px;
  border-radius: 100%;
  background-color: black;
  top: 100%;
  left: 0%;
  transform: translate(-50%, -50%);
  transition: background-color 0s, width 1.5s cubic-bezier(0.5, -0.5, 0.05, 1) 0s,
    height 1.5s cubic-bezier(0.5, -0.5, 0.05, 1) 0s;
  pointer-events: auto;
}

body .base .section-static {
  z-index: 94;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0px;
  left: 0px;
  transform-origin: 0% 0%;
  transition: width 1s cubic-bezier(0.5, -0.75, 0.05, 1), height 1s cubic-bezier(0.5, -0.75, 0.05, 1);
}

body .base .section-static:hover {
  background-color: rgb(48, 24, 24);
  color: gold;
}

body .base .section-static.top {
  transform: rotate(-45deg);
  border-bottom-right-radius: 400px;
}

body .base .section-static.bottom {
  transform: rotate(45deg);
  border-bottom-right-radius: 400px;
}

body .base.close {
  width: 300px;
  height: 300px;
  transition: all 1.25s cubic-bezier(0.5, 0, 0.05, 1.75);
}

body .base.close .menu {
  width: 150px;
  height: 150px;
  transition: all 1s cubic-bezier(0.5, 0, 0.05, 1.75), background-color 1s ease;
  background-color: gold;
}

body .base.close .menu .icon .bar,
body .base.close .menu .icon:before,
body .base.close .menu .icon:after {
  background-color: black;
}

body .base.close .menu .icon .bar {
  opacity: 0;
}

body .base.close .menu .icon:before,
body .base.close .menu .icon:after {
  transition: top 0.5s linear, transform 0.5s ease 0.5s, background-color 0.75s ease 0.25s;
}

body .base.close .menu .icon:before {
  top: 10px;
  transform: rotate(-45deg);
}

body .base.close .menu .icon:after {
  top: 10px;
  transform: rotate(45deg);
}

body .base.close .page-option {
  position: absolute;
  top: 0px;
  left: 0px;
}

body .base.close .page-option > * {
  position: absolute;
  font-size: 26px;
  color: #21264b;
  transition: 0.3s cubic-bezier(0.5, 0, 0.05, 1.75) 0.7s;
  pointer-events: none;
}

body .base.close .page-option .about-page {
  top: 8px;
  left: 175px;
  color: cyan;
  transform: rotate(10deg);
}

body .base.close .page-option .project-page {
  top: 115px;
  left: 115px;
  color: cyan;
  transition-delay: 0.65s;
  transform: rotate(40deg);
}

body .base.close .page-option .resume-page {
  top: 180px;
  left: 0px;
  color: cyan;
  transition-delay: 0.8s;
  transform: rotate(70deg);
}

body .base.close .section .cover1 {
  transform-origin: 100% 100%;
}

body .base.close .section .cover1,
body .base.close .section .cover1 .cover2,
body .base.close .section .cover1 .cover2 .content {
  position: absolute;
  width: 600px;
  height: 600px;
}

body .base.close .section .cover1,
body .base.close .section .cover1 .cover2 {
  top: 50%;
  left: 50%;
  transform: translate(-100%, -100%) rotate(16deg);
  overflow: hidden;
  transition: transform 0.5s ease-in 0.5s;
}

body .base.close .section .cover1 .cover2 {
  transform: translate(50%, -50%) rotate(-32deg);
  transform-origin: 0% 100%;
}

body .base.close .section .cover1 .cover2 .content {
  border-radius: 100%;
  background-color: rgb(0, 87, 90);
  top: 100%;
  left: 0%;
  transform: translate(-50%, -50%);
  transition: background-color 0s, width 1.1s cubic-bezier(0.5, 0, 0.05, 1.75) 0.25s,
    height 1.1s cubic-bezier(0.5, 0, 0.05, 2) 0.25s;
}

body .base.close .section .cover1 .cover2 .content:hover {
  background-color: #dd1350;
}

body .base.close .section-static {
  width: 300px;
  height: 300px;
  transition: width 1.25s cubic-bezier(0.5, 0, 0.05, 2), height 1.25s cubic-bezier(0.5, 0, 0.05, 2);
}
